@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body,
#root{
    width: 100vw;
    min-height: 100vh;
    font-family: 'Josefin Sans', sans-serif;
}

#app{
    width: 100vw;
    min-height: 100vh;
    position: relative;

}

.app-bg-image{
    width: 100%;
}

.app-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


.dark-text:hover{
    color:#D6D9E8;
    transition: all ease-in-out 3ms;
};

.light-text:hover{
    color: #525160;
    transition: all ease-in-out 3ms;
}








.header{  
    position: relative;
    width: 100%;
    margin-top: 4rem;
    padding-left: 2rem;
    padding-right: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}


.header-wrapper{
    display: -webkit-flex;
    display: flex;
     -webkit-justify-content: space-between;
             justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    
    width: 85%;
}


@media screen and (min-width: 768px){
    .header-wrapper{
        width: 60%;
    }
}

@media screen and (min-width: 1000px){
    .header-wrapper{
        width: 35%;
    }
}


.header > .header-wrapper h1{
    color: white;
    font-weight: 700;
    font-size: 1.3rem;
    letter-spacing: 0.5rem;
    pointer-events: none;
}

.header-wrapper > .svg-container{
    cursor: pointer;
}




.svg-theme{
    margin: 1rem 0;  
}


.todo-container{
    position: absolute;
    top: 5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 80%;
}


.todo-container li:hover {
   color: #D6D9E8;
}

@media screen and (min-width: 768px) {
    .todo-container{
        width: 60%;
    }
}

@media screen and (min-width: 1000px) {
    .todo-container{
        width: 35%;
    }
}

.todo-form{
    width: 100%;
    padding: 0.8rem;
    border-radius: 4px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;        
}

.input-circle{
    width: 1.4rem;
    padding: 0.6rem;
    height: 1.4rem;
    border-radius: 50%;
    border: 1.5px solid;
    margin-right: 0.6rem;
    margin-left: 0.2rem;
}

.todo-input{
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    background-color: transparent;
    color: #bdbcc2ee;
    caret-color: #4D73C3;
}

.todo-input::-webkit-input-placeholder{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 0.7rem;
}

.todo-input:-ms-input-placeholder{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 0.7rem;
}

.todo-input::placeholder{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 0.7rem;
}

@media screen and (min-width: 768px){
    .todo-input::-webkit-input-placeholder{
        font-size: 0.8rem;
    }
    .todo-input:-ms-input-placeholder{
        font-size: 0.8rem;
    }
    .todo-input::placeholder{
        font-size: 0.8rem;
    }

}

.dark-placeholder::-webkit-input-placeholder{
    color: #5D5F76;
}

.dark-placeholder:-ms-input-placeholder{
    color: #5D5F76;
}

.dark-placeholder::placeholder{
    color: #5D5F76;
}

.light-placeholder::-webkit-input-placeholder{
    color: #bcbbc0;
}

.light-placeholder:-ms-input-placeholder{
    color: #bcbbc0;
}

.light-placeholder::placeholder{
    color: #bcbbc0;
}



.todo-list{
    margin-top: 1.5rem; 
    font-size: 0.8rem;
    border-radius: 4px;
}

@media screen and (min-width: 900px){
    .todo-list{
        font-size: 0.9rem;
    }
}

.todo-item{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.7rem;
    list-style: none;
    position: relative;
    cursor: pointer;
}

.todo-item:first-child{
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.todo-item:not(first-child){
    border-top: 1.3px solid;

}



.todo-text-check{
    padding: 0.5rem 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: 0.8 1;
            flex: 0.8 1;
}





.circle-gradient{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    margin-right: 0.6rem;
    margin-left: 0.2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #47495a;
}



.todo-circle{
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
 
   
}

.dark-bg-circle{
    background-color: #25273C;
}

.light-bg-circle{
    background-color: #FFFFFF;

}

 .circle-gradient:hover{
    background: linear-gradient(94deg, rgba(121,189,242,1) 0%, rgba(136,162,249,1) 50%, rgba(158,122,244,1) 100%);
    transition: all ease-in-out 3ms;
}



.checked {
background: linear-gradient(94deg, rgba(121,189,242,1) 0%, rgba(136,162,249,1) 50%, rgba(158,122,244,1) 100%);
}


.icon-cross{
    padding: 1rem;
    position: absolute;
    right: 1rem;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    -webkit-flex: 0.2 1;
            flex: 0.2 1;
}

.icon-checked{
    -webkit-transform: scale(0.7);
            transform: scale(0.7)
}

.todo-filters{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.6rem 1.2rem;
    font-size: 0.7rem;
    border-top: 1.5px solid;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

@media screen and (min-width: 900px){
    .todo-filters{
        font-size: 0.8rem;
    }
}



.items-left, .clear-completed{
    cursor: pointer;
    padding: 0.6rem 0;
    
}

.filters-desktop {
    list-style: none;
    display: none;
    
}

@media screen and (min-width:768px) {
    .filters-desktop{
        display: -webkit-flex;
        display: flex;  
    }
}

.filters-desktop > li{
    padding: 0 0.3rem;
    cursor: pointer;
}

.filters-container-mobile {
    margin-top: 0.8rem;
    padding: 0.8rem 0;
    font-size: 0.85rem;
    border-radius: 4px;
}


.filters-mobile{
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 4px;
}

@media screen and (min-width: 768px){
    .filters-container-mobile{
        display: none;
    }
}

.filters-mobile > li{
    cursor: pointer;
    margin: 0 0.7rem;
}

.filters-mobile > li > div >{
    width: 100%;
    height: 100%;
}


.filters-mobile > li > div > .nav-link {
    width: 100%;
    height: 100%;
}


.active-dark{
    color: #4D73C3;
}

.active-light{
    color: #5686DE;
}





.drag-drop{
    margin: 3rem 0;
    text-align: center;
    font-size: 0.7rem;
}

@media screen and (min-width: 900px){
    .drag-drop{
        font-size: 0.8rem;
    }
}


.dark-text2{
    color: #5D5F76;
}

.light-text2{
    color: #bcbbc0;
}
